import React from 'react'
import {SliderContainer } from './slider.styles'

const Slider = (props) => {
    
    return (
        
        <SliderContainer onClick={()=>props.detail ? window.open(props.link) : props.handleClick()} detail={props.detail}>
            <img src={props.img} width='100%' height='100%' alt=''/>
        </SliderContainer>
    )
}

export default Slider