import styled, {css} from 'styled-components'

export const ProjectsContainer = styled.div`
    /* flex:1; */
    
    width:35%;
    display: flex;
    flex-flow: column;
    background-color: black;
    justify-content: center;
    align-items: center;
    font-size: 28px;
    font-weight: bold;
`

export const TitleContainer = styled.p`
    color:gray;
 
    ${(props)=>props.detail}
    ${(props)=>props.hidden&&'visibility:hidden'}
    &:hover {
        cursor:pointer;
    }

    ${(props)=>props.curr&&css`color:white;`} /* change color on mouse hover */
`

export const ContentContainer = styled.p`
    font-family: 'Sunflower', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size:20px;   
    font-weight:normal;
    margin-top:50px;
`

export const TagsContainer = styled.div`
    font-family: 'Sunflower', sans-serif;
    display:flex;
    
    display: inline-block;
    border-radius: 0.6rem/0.8rem;
    background-color: white;
    
    align-items:center;
    margin:50px;

`

export const TagContainer = styled.div`
    border-radius: 0.6rem/0.8rem;
    font-family: 'Sunflower', sans-serif;
    display: inline-block; 
    color: black;
    font-weight:normal;
    font-size:16px;
    margin:5px;
    padding:6px;
    background-color: #c6b79f;
`

export const Logo = styled.img`
    height:3vh;
    margin:0.5vh;
    cursor:pointer;
    color:white;
    background-color:white;
`

export const LogosContainer = styled.div`
    display: inline-block;
    border-radius: 0.6rem/0.8rem;
    background-color: white;
    display:flex;
    flex-flow:column;
    align-items:center;
    position:absolute;
    bottom:50%;
    right:20px;
`