import React, {useState} from 'react'
import { ProjectsContainer, TitleContainer, ContentContainer, TagsContainer, TagContainer, Logo, LogosContainer } from './projects.styles'
import {ReactComponent as Github} from '../../assets/github.svg'
import arrowLeft from '../../assets/arrow-left.svg'
import info from '../../assets/info.svg'
const Projects = (props) => {
    const items = [
        {title:'Clothing Shop', content:'E-Commerce website', keyword:['#React', '#react-router-dom', '#redux', '#styled-components','#firebase'], ghlink:'https://github.com/wer153/clothing-shop', link:'https://wer153.github.io/clothing-shop/'},
        {title:'Keeper', content:'Basic To-do list webapp', keyword:['#React'], ghlink:'https://github.com/wer153/Keeper', link:'https://wer153.github.io/Keeper/'},
        {title:'Mastermind', content:'Popular table game in webapp version', keyword:['#React','#first-project','colorful-components','css','html'],ghlink:'https://github.com/wer153/Mastermind.io', link:'https://wer153.github.io/Mastermind.io/'},
        {title:'Weather App', content:'Weathercast webapp', keyword:['#Node', '#Express','#Geocode', '#Weathercast','#API'],ghlink:'https://github.com/wer153/weather-app', link:'https://minki-weather-app.herokuapp.com/'}
    ]

    const [infoView, setInfoView] = useState(false)
    return (
    <ProjectsContainer>
        {items.map((item,index)=>(
            <TitleContainer 
                key={index} 
                curr={index===props.id} 
                onMouseOver={()=>props.handleMouseOver(index,item.link)}
                onClick={()=>window.open(item.ghlink)}
                hidden={props.detail && index!==props.id}
                detail={props.detail}
            >
                {index===props.id && <Github/>}{item.title}

            </TitleContainer>    
        ))}
        {props.detail &&
        <ContentContainer>
            {items[props.id].content}
        </ContentContainer>}

        {props.detail &&
        <TagsContainer>
        {items[props.id].keyword.map((tag)=>(
            <TagContainer>
                {tag}
            </TagContainer>
        ))}
        </TagsContainer>}

        {props.detail &&
            <Logo onClick={()=>props.handleClick()} src={arrowLeft}/>
        }
        <LogosContainer>
            <Logo src={info} onClick={()=>setInfoView((prev)=>!prev)}/>
            {infoView && <TagContainer>#Learn More</TagContainer>}
            {infoView && <TagContainer>#By Click Image</TagContainer>}
            {infoView && <TagContainer>#By Click Title</TagContainer>}
        </LogosContainer>
    </ProjectsContainer>)
}

export default Projects