import styled, {css} from 'styled-components'


const detailStyle = css`
    width:45vw;
`

export const SliderContainer = styled.div`
    width:30vw;
    ${props=>props.detail && detailStyle }

    /* background-color:black; */
    margin:auto;
    overflow: hidden;

    display: flex;
    justify-content: center;
    align-items: center;

    &:hover{
        cursor:pointer;
    }
`